import React, { Component } from 'react';

class Scorer extends Component {
  render() {
    return (
      <>
        <p>Scorer Name: {this.props.scorer.name}</p>
        <p>Goals Scored: {this.props.scorer.goals}</p>

        <input type="text" value={this.props.scorer.goals} onChange={this.props.changed} />

        <hr />
      </>
    );
  }
}

export default Scorer;
